<template>
  <div>
    <!-- 面包屑区域 -->
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>商品管理</el-breadcrumb-item>
      <el-breadcrumb-item>商品列表</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 卡片区域 -->
    <el-card class="box-card">
      <el-row :gutter="20">
        <el-col :span="8">
          <el-input placeholder="请输入要查询的关键字" v-model="querInfo.query" clearable @clear="getGoriesList">
            <el-button slot="append" icon="el-icon-search"  @click="getGoriesList"></el-button>
          </el-input>
        </el-col>
        <el-col :span="4">
          <el-button type="primary" @click="addGoods">添加商品</el-button>
        </el-col>
      </el-row>
      <!-- 表格区域 -->
      <el-table stripe :data="goriseList" border style="width: 100%">
        <el-table-column type="index" label="#" ></el-table-column>
        <el-table-column prop="goods_name" label="商品名称" width="500"></el-table-column>
        <el-table-column prop="goods_price" label="商品价格(元)" ></el-table-column>
        <el-table-column prop="goods_weight" label="商品重量" ></el-table-column>
        <el-table-column prop="add_time" label="添加时间" width="170">
          <template slot-scope="scope">
            {{ scope.row.add_time | dateFormat }}
          </template>
        </el-table-column>
        <el-table-column prop="goods_number" label="操作" width="150">
          <template slot-scope="scope">
            <el-tooltip class="item"  effect="dark" content="修改" placement="top">
              <el-button type="primary" @click="getGooddata(scope.row.goods_id)" icon="el-icon-edit"> </el-button>
            </el-tooltip>
            <el-tooltip class="item" effect="dark" content="删除" placement="top">
              <el-button type="danger" @click="removeGood(scope.row)" icon="el-icon-delete"></el-button>
            </el-tooltip>
            </template>
        </el-table-column>
      </el-table>
      <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="querInfo.pagenum"
            :page-sizes="[7, 15, 30, 100]"
            :page-size="querInfo.pagesize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="total">
      </el-pagination>
    </el-card>

    <!-- 修改商品信息的对话框 -->
    <el-dialog
      title="修改商品信息"
      :visible.sync="modificationDialogVisible"
      width="30%">
      <el-form :model="getgoodsForm" :rules="alterGoodsRules" ref="alterGoodsRef" label-width="100px">
        <el-form-item label="商品名称：" prop="goods_name">
        <el-input v-model="getgoodsForm.goods_name"></el-input>
        </el-form-item>
        <el-form-item label="商品价格：" prop="goods_price">
        <el-input v-model="getgoodsForm.goods_price"></el-input>
        </el-form-item>
        <el-form-item label="商品数量：" prop="goods_number">
        <el-input v-model="getgoodsForm.goods_number"></el-input>
        </el-form-item>
        <el-form-item label="商品重量：" prop="goods_weight">
        <el-input v-model="getgoodsForm.goods_weight"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="modificationDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="alterGoodsDataOk">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data () {
    return {
      // 查询数据库条件
      querInfo: {
        query: '',
        pagenum: 1,
        pagesize: 7
      },
      // 定义一个空数组保存数据
      getgoodsForm: [],
      // 修改时要提交到数据库的数据
      goodsId: '',
      alterGoodsForm: {
        goods_name: '',
        goods_price: '',
        goods_number: '',
        goods_weight: '',
        goods_introduce: '',
        pics: '',
        attrs: ''
      },
      // 匹配验证
      alterGoodsRules: {
        goods_name: [
          { required: true, message: '请输入商品名称', trigger: 'blur' }
        ],
        goods_price: [
          { required: true, message: '请输入商品价格', trigger: 'blur' }
        ],
        goods_number: [
          { required: true, message: '请输入商品数量', trigger: 'blur' }
        ],
        goods_weight: [
          { required: true, message: '请输入商品重量', trigger: 'blur' }
        ]
      },
      // 打开修改商品对话框的值
      modificationDialogVisible: false,
      goriseList: [],
      total: 0
    }
  },
  created () {
    this.getGoriesList()
  },
  methods: {
    async getGoriesList () {
      const { data: res } = await this.$http.get('goods', { params: this.querInfo })
      if (res.meta.status !== 200) {
        return this.$message.error('获取商品分类数据失败')
      }
      this.goriseList = res.data.goods
      this.total = res.data.total
    },
    addGoods () {
      this.$router(this.$router.push('/goods/add'))
    },
    // 根据商品id查询数据
    async getGooddata (id) {
      const { data: res } = await this.$http.get('goods/' + id)
      if (res.meta.status !== 200) {
        return this.$message.error('获取商品信息失败！')
      }
      this.getgoodsForm = res.data
      this.goodsId = res.data.goods_id
      this.alterGoodsForm.goods_name = res.data.goods_name
      this.alterGoodsForm.goods_price = res.data.goods_price
      this.alterGoodsForm.goods_number = res.data.goods_number
      this.alterGoodsForm.goods_weight = res.data.goods_weight
      this.modificationDialogVisible = true
    },
    // 确定修改商品信息
    alterGoodsDataOk () {
      this.$refs.alterGoodsRef.validate(async valid => {
        if (!valid) return
        const { data: res } = await this.$http.put('goods/' + this.goodsId, this.alterGoodsForm)
        if (res.meta.status !== 200) return this.$message.error('因为数据库中缺少一个商品分类属性，所以无法修改商品信息！')
        this.$message.sccuess('修改商品信息成功！')
        this.modificationDialogVisible = false
      })
    },
    // 删除商品
    async removeGood (id) {
      const abc = await this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).catch(error => error)
      if (abc !== 'confirm') {
        return this.$message.info('已取消删除')
      }
      const { data: res } = await this.$http.delete('goods/' + id.goods_id)
      if (res.meta.status !== 200) {
        return this.$message.error('删除商品失败！')
      }
      this.$message.success('商品已成功删除')
      this.getGoriesList()
    },
    handleSizeChange (newSize) {
      this.querInfo.pagesize = newSize
      this.getGoriesList()
    },
    handleCurrentChange (newNum) {
      this.querInfo.pagenum = newNum
      this.getGoriesList()
    }
  }
}
</script>

